export default {
	data() {
		return {
			exam_list:[],
		}
	},
	onLoad() {
		
		var t=this;
		uni.showLoading();
		//考试列表
		var data={
			is_exam:2,
			communist_no:t.globalData.userInfo.communist_no,
		}
		t.urlRequest.urlRequest(t.func.examList,data,function(res){
			t.exam_list=res.data.exam_list;
			uni.hideLoading();
		})
		
	},
	methods: {
		startTest:function(id){
			uni.navigateTo({
				url: 'test-info?id='+id
			})
		},
		examDetail:function(id){
			uni.navigateTo({
				url: 'exam-detail?id='+id
			})
		}
	}
}
